import { Component, Element, h, Host, Method, Prop } from '@stencil/core';

@Component({
  tag: 'storm-vertical-split-view',
  styleUrl: 'storm-vertical-split-view.css',
  shadow: false,
})
export class StormVerticalSplitView {
  private _splitViewRef: HTMLStormSplitViewElement;

  @Element() host: HTMLElement;
  @Prop() splitPercent: number = 50;
  @Prop() fixedSize?: string;
  @Prop() fixedPane?: 'left' | 'right';
  @Prop({ mutable: true }) leftPaneVisible?: boolean;
  @Prop({ mutable: true }) rightPaneVisible?: boolean;

  @Method()
  async viewToggle(token: 'left' | 'right', force: boolean) {
    await this._splitViewRef.viewToggle(token === 'left' ? 'first' : 'second', force);
  }

  componentWillLoad() {
    this.leftPaneVisible = this.leftPaneVisible ?? this.host.querySelector(':scope>[slot="left-pane"]') != null;
    this.rightPaneVisible = this.rightPaneVisible ?? this.host.querySelector(':scope>[slot="right-pane"]') != null;
  }

  render() {
    return (
      <Host>
        <storm-split-view
          ref={elm => (this._splitViewRef = elm)}
          splitPercent={this.splitPercent}
          fixedSize={this.fixedSize}
          fixedPane={this.fixedPane == null ? null : this.fixedPane == 'left' ? 'first' : 'second'}
          firstPaneVisible={this.leftPaneVisible}
          secondPaneVisible={this.rightPaneVisible}
          direction={'vertical'}
          firstPaneName={'left-pane'}
          secondPaneName={'right-pane'}
        >
          <slot name="left-pane"></slot>
          <slot name="right-pane"></slot>
        </storm-split-view>
      </Host>
    );
  }
}
